* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#body {
  min-height: 30vh;
  min-width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  margin: 0px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.header {
  background-color: #f6f6f642;
  color: white;
  width: 100%;
  text-align: center;
  /* height: 300px; */
  padding: 50px;
}

.header-title {
  font-size: 50px;
}

.header-text {
  font-size: 20px;
}

.section {
  flex-flow: row wrap;
  width: 90%;
  display: flex;
  flex-direction: row wrap;
  align-items: center;
  justify-content: center;
}

.section > div {
  padding: 30px;
}

.small-div {
  width: 20%;
}

.big-div {
  width: 80%;
}

.bg-grey {
  background-color: #f6f6f636;
}

.bg-darkgrey {
  background-color: #f6f6f618;
}

.bg-lightgrey {
  background-color: #f6f6f654;
}

.div-title {
  font-size: 30px;
  font-weight: 600;
}

.div-title-2 {
  font-size: 30px;
  font-weight: 600;
  text-align: center;
}

.div-text {
  font-size:20px;
}

.contact-form {
  display: flex;
  flex-direction: row;
  /*flex-flow: row wrap;*/
  justify-content: space-between;
  width: 100%;
}

#sect1 {
  width: 100%;
}

#sect2 {
  width: 100%;
}

img {
  width: 100px;
  height: 100px;
}

.login-holder {
  justify-content: center;
}

